export default{
	name:'navs',
	data() {
		return {
			num:0,
			navalue: [
				{
					'name':'宝贝',
				},
				{
					'name':'详情',
				},
				{
					'name':'评价',
				}
			]
		}
	},
	
	methods:{
		navbtn(index){
			this.num = index
			// console.log(index)
			if(index === 0){
				this.backTop()
			}else{
				// console.log('不是点击宝贝tab')
				this.$parent.fatherTab(index)
			}
		},
		
		// 回到顶部
		backTop(){
			wx.pageScrollTo({
				scrollTop:0
			})
		}
		
	}
	
}
